import { useState, useMemo } from 'react';
import { Link } from 'react-router-dom';
import { FormProvider, useForm } from 'react-hook-form';

import { Icon } from '@iconify/react';
import { toast } from 'react-toastify';

import { API_AUTH, HOST_API } from '../../config';
import axios from '../../utils/axios';
import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';

import OtpVerifyDialog from '../../sections/profile/VerifyCode';
import ChangeLanguage from '../../sections/profile/ChangeLanguage';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';
import GradientBorderWrapper, { Divider } from '../../components/wrappers/GradientBorderWrapper';
import { FloatingInput } from '../../components/form/FloatingInput';
import ChangeAddress from './ChangeAddress';

export default function BasicProfile() {
    const { currentLanguage, t } = useLocales();
    const { user, initialize } = useAuth();
    const [file, setFile] = useState(user?.avatar || '');
    const [showBank, setShowBank] = useState(false);
    const [showAddress, setShowAddress] = useState(false);
    const [loading, setLoading] = useState(false);

    const imageChange = (event) => {
        if (event.target.files && event.target.files.length > 0) {
            setFile(event.target.files[0]);
            const form = new FormData();
            form.append('file', event.target.files[0]);
            form.append('originFile', event.target.files[0]?.path);

            axios.put(API_AUTH.userInfo, form).then((res) => {
                if (res.status === 200) initialize();
            });
        }
    };

    const { register, handleSubmit } = useForm({
        defaultValues: useMemo(() => {
            return {
                bank: user?.bank
            };
        }, [])
    });

    const compareObjects = (a, b) => {
        for (let key in a) {
            if (typeof (a[key]) != "object" && a.hasOwnProperty(key) && b.hasOwnProperty(key)) {
                if (a[key] !== b[key]) {
                    return false;
                }
            }
        }
        return true;
    }

    const saveBankDetail = (data) => {
        setLoading(true);
        if (compareObjects(data, user) && compareObjects(data.bank, user.bank))
            return;
        axios.put(API_AUTH.userInfo, data)
            .then((res) => {
                if (res.status === 200) {
                    initialize();
                    toast.success(res.data?.message);
                    setShowBank(false);
                }
                else
                    toast.warning(res.data?.message || res.data?.data);
                setLoading(false);
            })
            .catch((err) => { setLoading(false); })
            .finally(() => {
                setLoading(false);
            });
    };

    const handleAddAddress = () => {
        setShowAddress(true);
    };

    const handleAddBank = () => {
        setShowBank(true);
    };

    const handleCancelBank = () => {
        setShowBank(false);
    };
    console.log("====>BasicProfile");
    return (
        <Page title="Profile">
            <PageBanner>
                <div className="container mb-16">
                    <div className="flex flex-col gap-8 items-center">
                        {/* avatar  */}
                        <div className="flex flex-col items-center gap-8">
                            <div className="avatar w-[200px] h-[200px] relative">
                                <img
                                    src={typeof file === 'string' ? `${HOST_API}${file}` : URL.createObjectURL(file)}
                                    alt=""
                                    className="rounded-full"
                                />
                                <label
                                    htmlFor="avatar"
                                    className="bottom-0 right-0 absolute w-12 h-12 rounded-full bg-secondary flex items-center justify-center cursor-pointer text-[20px]"
                                >
                                    <Icon icon="ic:round-edit" className="text-primary" />
                                </label>
                                <input
                                    type="file"
                                    accept=".png, .jpg, .jpeg, .bmp, .webp"
                                    className="hidden"
                                    id="avatar"
                                    onChange={imageChange}
                                />
                            </div>
                            <label className="text-3xl">{user?.fullName}</label>
                        </div>
                    </div>
                </div>
            </PageBanner>
            <div className="container">
                <div className="flex flex-col gap-4 items-center">
                    <div className="flex flex-col items-center gap-2 w-full px-2">
                        <label className="text-[28px] w-full text-left">{t('title.profile')}</label>
                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-2 w-full p-6">
                                {/* email  */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.email_address')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{user?.email}</p>
                                        {user?.emailVerification ? (
                                            <Icon icon="ic:baseline-verified-user" className="text-success" />
                                        ) : (
                                            <Icon icon="octicon:unverified-24" className="text-error" />
                                        )}
                                    </div>
                                    <div className="flex gap-4 mb-4">
                                        <Link
                                            to="changemail"
                                            htmlFor="email-modal-check"
                                            className="text-sm text-primary cursor-pointer underline"
                                        >
                                            {t('action.edit')}
                                        </Link>
                                        {/* <label htmlFor="email-verify-check" className="text-primary cursor-pointer">
                                            {t('action.verify')}
                                        </label> */}
                                    </div>

                                    <Divider cls="mt-1" />
                                </div>
                                {/* phone */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.telephone')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{user?.phone}</p>
                                        {user?.phone &&
                                            (user?.phoneVerification ? (
                                                <Icon icon="ic:baseline-verified-user" className="text-success" />
                                            ) : (
                                                <Icon icon="octicon:unverified-24" className="text-error" />
                                            ))}
                                    </div>
                                    <div className="flex gap-4 mb-4">
                                        <Link
                                            to="changtelephone"
                                            htmlFor="phone-modal-check"
                                            className="text-sm text-primary cursor-pointer underline"
                                        >
                                            {t('action.edit')}
                                        </Link>
                                        {/* <label htmlFor="phone-verify-check" className="text-primary cursor-pointer">
                                            {t('action.verify')}
                                        </label> */}
                                    </div>
                                    <Divider cls="mt-1" />
                                </div>
                                {/* adress */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('label.address')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{`${user?.address?.country} ${user?.address?.city}`}</p>

                                    </div>
                                    <div className="flex gap-4 mb-4">
                                        {!showAddress ? (
                                            <Link className="text-sm text-primary underline mb-4" onClick={handleAddAddress}>
                                                {t('action.add')}
                                            </Link>
                                        ) : (
                                            <ChangeAddress setShowAddress={setShowAddress} compareObjects={compareObjects} />
                                        )}
                                    </div>
                                    <Divider cls="mt-1" />
                                </div>
                                {/* id verification  */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.identity_verification')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{t('action.no_required')}</p>
                                    </div>
                                    <Link to="/profile/identify" className="text-sm text-primary underline mb-4">
                                        {t('action.show')}
                                    </Link>
                                    <Divider cls="mt-1" />
                                </div>
                                {/* following  */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.following')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{t('description.following')}</p>
                                    </div>
                                    <p className="text-sm ursor-pointer text-primary underline mb-4">{t('action.show')}</p>
                                    <Divider cls="mt-1" />
                                </div>

                                {/* social */}
                                <div className="flex flex-col gap-1">
                                    <label className="text-[28px] font-medium mb-2">{t('title.social')}</label>
                                    <div className="flex flex-col gap-1 items-center mb-2 w-full">
                                        <div className="flex justify-between w-full">
                                            <div className="flex gap-2 items-center text-stone-400">
                                                <Icon icon="bi:facebook" />
                                                <p>Facebook</p>
                                            </div>
                                            {user?.social?.facebook ? (
                                                <Icon
                                                    icon="ic:baseline-verified-user"
                                                    className="text-success  underline"
                                                />
                                            ) : (
                                                <p className="text-sm cursor-pointer text-primary  underline">
                                                    {t('action.verify')}
                                                </p>
                                            )}
                                        </div>
                                    </div>
                                    <div className="flex flex-col gap-1 items-center mb-2 w-full">
                                        <div className="flex justify-between w-full">
                                            <div className="flex gap-2 items-center text-stone-400">
                                                <Icon icon="mingcute:twitter-line" />
                                                <p>Twitter</p>
                                            </div>
                                            {user?.social?.twitter ? (
                                                <Icon icon="ic:baseline-verified-user" className="text-success" />
                                            ) : (
                                                <p className="text-sm cursor-pointer text-primary">{t('action.verify')}</p>
                                            )}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <div className="flex flex-col items-center gap-2 w-full p-2">
                        <label className="text-[28px] w-full text-left">{t('title.financial')}</label>
                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-2 w-full p-6">
                                {/* Finance  */}
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.payments')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{t('description.payments')}</p>
                                    </div>
                                    <Link to="/profile/payments" className="text-sm text-primary underline mb-4">
                                        {t('action.show')}
                                    </Link>
                                    <Divider cls="mt-1" />
                                </div>
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('title.bank_details')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">123 - 4567 - ... 890</p>
                                    </div>
                                    {!showBank ? (
                                        <Link className="text-sm text-primary underline mb-4" onClick={handleAddBank}>
                                            {t('action.add')}
                                        </Link>
                                    ) : (
                                        <FormProvider>
                                            <form onSubmit={handleSubmit(saveBankDetail)}>
                                                <div className="grid md:grid-cols-2 py-4 gap-7 text-stone-400">
                                                    {/* bank infor */}
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.bank_code')}
                                                            label={t('label.bank_code')}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.legal_name')}
                                                            label={t('label.bank_fullname')}
                                                            length={30}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.agencia')}
                                                            label={t('label.bank_branch_code')}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.agencia_dv')}
                                                            label={t('label.bank_branch_check')}
                                                            required={false}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.conta')}
                                                            label={t('label.bank_account')}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.conta_div')}
                                                            label={t('label.bank_account_check')}
                                                            required={false}
                                                        />
                                                    </div>
                                                    <div>
                                                        <FloatingInput
                                                            register={register('bank.document_number')}
                                                            label={t('label.CPF')}
                                                        />
                                                    </div>


                                                    <div>
                                                        <div className="flex gap-4 justify-end">
                                                            <button
                                                                type='button'
                                                                className="btn btn-primary btn-outline px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto"
                                                                onClick={handleCancelBank}
                                                            >
                                                                {t('action.cancel')}
                                                            </button>
                                                            <button type='submit' className="btn btn-primary px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto">
                                                                {t('action.save_data')}
                                                            </button>
                                                        </div>
                                                    </div>
                                                </div>
                                            </form>
                                        </FormProvider>
                                    )}
                                </div>
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <div className="flex flex-col items-center gap-2 w-full p-2">
                        <label className="text-[28px] w-full text-left">{t('title.settings')}</label>
                        <GradientBorderWrapper>
                            <div className="flex flex-col gap-2 w-full p-4">
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('label.email_preferences')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">{t('label.email_preferences_desc')}</p>
                                    </div>
                                    <p className="text-sm cursor-pointer text-primary underline mb-4">
                                        <label htmlFor="email-modal-check">{t('action.edit')}</label>
                                    </p>
                                    <Divider cls="-mt-0" />
                                </div>
                                <div className="flex flex-col">
                                    <label className="text-[28px] font-medium">{t('label.language')}</label>
                                    <div className="flex gap-2 items-center mb-2">
                                        <p className="text-stone-400">
                                            {currentLanguage === 'en' ? 'English' : 'Português'}
                                        </p>
                                    </div>
                                    <label
                                        htmlFor="language-modal-check"
                                        className="text-sm cursor-pointer text-primary underline mb-4"
                                    >
                                        {t('action.edit')}
                                    </label>
                                </div>
                            </div>
                        </GradientBorderWrapper>
                    </div>
                    <div className="flex justify-center gap-4 w-full max-w-2xl mb-8">
                        <p className="cursor-pointer underline text-primary">{t('action.export_my_data')}</p>
                        <p className="cursor-pointer underline text-error">{t('action.remove_my_account')}</p>
                    </div>
                </div>
            </div>
            {/* <ChangeEmailDialog user={user} /> */}
            {/* <ChangePhoneDialog user={user} /> */}
            <ChangeLanguage />
            {/* <OtpVerifyDialog user={user} address={user?.email} id="email-verify-check" method="email" /> */}
            {/* <OtpVerifyDialog address={user?.phone} id="phone-verify-check" /> */}
        </Page>
    );
}
