import PropTypes from 'prop-types';

import { Controller } from 'react-hook-form';

import { FormControl, Select, InputLabel, FormHelperText } from '@mui/material';

RHFSelect.propTypes = {
    children: PropTypes.node,
    name: PropTypes.string
};

export default function RHFSelect({ name, control, children, label, ...other }) {
    return (
        <Controller
            name={name}
            control={control}
            render={({ field, fieldState: { error } }) => (
                <FormControl error={!!error} >
                    <InputLabel id={`select-label${label}`} sx={{ color: "white !important" }}  >{label}</InputLabel>
                    <Select
                        {...field}
                        labelId={`select-label${label}`}
                        label={label}
                        error={!!error}
                        {...other}
                        sx={{
                            color: "white",
                            "& fieldset": { borderColor: "#b0b0b0 !important", borderRadius: 4 },
                            "& svg": {
                                color: "white"
                            }
                        }}
                    >
                        {children}
                    </Select>
                    <FormHelperText>{error?.message}</FormHelperText>
                </FormControl>
            )}
        />
    );
}