import { useMemo, useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { MenuItem } from '@mui/material';
import { FormProvider, useForm } from 'react-hook-form';
import { toast } from 'react-toastify';

import useAuth from '../../hooks/useAuth';
import useLocales from '../../hooks/useLocales';
import { API_AUTH } from '../../config';
import axios from '../../utils/axios';

import RHFSelect from '../../components/form/RHFSelect';
import { FloatingInput } from '../../components/form/FloatingInput';

import { Country, State, City } from 'country-state-city';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

export default function ChangeAddress({ setShowAddress, compareObjects }) {
    const { t } = useLocales();
    const { user, initialize } = useAuth();
    const [loading, setLoading] = useState(false);

    const countries = Country.getAllCountries();
    const [states, setStates] = useState([]);
    const [cities, setCities] = useState([]);


    const { handleSubmit, watch, setValue, control, register } = useForm({
        defaultValues: useMemo(() => {
            return {
                country: user?.address?.country || "",
                state: user?.address?.state || "",
                city: user?.address?.city || "",
                neighborhood: user?.address?.neighborhood || "",
                street: user?.address?.street || "",
                street_number: user?.address?.street_number || "",
                zipcode: user?.address?.zipcode || ""
            };
        }, [])
    });

    const countryValue = watch('country');
    const stateValue = watch('state');
    const cityValue = watch('city');

    const saveAddress = (data) => {
        setLoading(true);
        if (compareObjects(data, user?.address))
            return;
        axios.put(API_AUTH.userInfo, { address: data })
            .then((res) => {
                if (res.status === 200) {
                    initialize();
                    toast.success(res.data?.message);
                    setShowAddress(false);
                }
                else
                    toast.warning(res.data?.message || res.data?.data);
                setLoading(false);
            })
            .catch((err) => { setLoading(false); })
            .finally(() => {
                setLoading(false);
            });
    };

    const handleCancelAddress = () => {
        setShowAddress(false);
    };

    useEffect(() => {
        const data = State.getStatesOfCountry(countryValue);
        const state = data.filter((item) => item.isoCode === stateValue);
        if (!state.length) setValue('state', '');
        setStates(data);
    }, [countryValue])

    useEffect(() => {
        const data = City.getCitiesOfState(countryValue, stateValue);
        const state = data.filter((item) => item.name === cityValue);
        if (!state.length) setValue('city', '');
        setCities(data);
    }, [stateValue])

    return (
        <FormProvider>
            <form onSubmit={handleSubmit(saveAddress)} className="w-full">
                <div className="flex flex-col w-full">
                    <div className="grid md:grid-cols-3 py-4 gap-7 text-stone-400">
                        {/* bank infor */}
                        <RHFSelect
                            name="country"
                            control={control}
                            label={t("label.select_country")}
                            MenuProps={MenuProps}
                            required={true}
                        >
                            {countries.map((country, i) => (
                                <MenuItem
                                    key={i}
                                    value={country.isoCode}
                                >
                                    {`${country.name} (${country.isoCode})`}
                                </MenuItem>
                            ))}
                        </RHFSelect>

                        <RHFSelect
                            name="state"
                            control={control}
                            label={t("label.state")}
                            MenuProps={MenuProps}
                            required={true}
                        >
                            {states.length ? states.map((state, i) => (
                                <MenuItem
                                    key={i}
                                    value={state.isoCode}
                                >
                                    {state.name}
                                </MenuItem>
                            )) : (
                                <MenuItem>
                                    {t("description.select_country")}
                                </MenuItem>
                            )}
                        </RHFSelect>

                        <RHFSelect
                            name="city"
                            control={control}
                            label={t("label.city")}
                            MenuProps={MenuProps}
                            required={true}
                        >
                            {cities.length ? cities.map((city, i) => (
                                <MenuItem
                                    key={i}
                                    value={city.name}
                                >
                                    {city.name}
                                </MenuItem>
                            )) : (
                                <MenuItem>
                                    {t("description.select_state")}
                                </MenuItem>
                            )}
                        </RHFSelect>
                    </div>
                    <div className="grid md:grid-cols-4 py-4 gap-7 text-stone-400">
                        <FloatingInput
                            register={register('neighborhood')}
                            label={t('label.neighborhood')}
                        />

                        <FloatingInput
                            register={register('street')}
                            label={t('label.street')}
                        />

                        <FloatingInput
                            register={register('street_number')}
                            label={t('label.street_number')}
                        />

                        <FloatingInput
                            register={register('zipcode')}
                            label={t('label.zipcode')}
                        />

                    </div>
                    <div>
                        <div className="flex gap-4 justify-end">
                            <button
                                type='button'
                                className="btn btn-primary btn-outline px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto"
                                onClick={handleCancelAddress}
                            >
                                {t('action.cancel')}
                            </button>
                            <button type='submit' className="btn btn-primary px-8 capitalize font-normal py-4 rounded-2xl text-[19px] h-auto">
                                {t('action.save_data')}
                            </button>
                        </div>
                    </div>
                </div>
            </form>
        </FormProvider>
    );
}
