import { useState } from 'react';
import Select from 'react-select';

import useLocales from '../../hooks/useLocales';

import { BlurModalBox } from '../../components/StyledComponents';

const options = [
    { value: 'en', label: 'English' },
    { value: 'pt', label: 'Português' }
];

const colourStyles = {
    control: (styles) => ({
        ...styles,
        backgroundColor: 'transparent',
        boxShadow: 'none !important',
        borderColor: 'hsl(0, 0%, 80%) !important'
    }),
    singleValue: (styles) => ({ ...styles, color: '#fff' }),
    menu: (styles) => ({ ...styles, backgroundColor: '#1c1c1c' }),
    option: (styles, { isFocused }) => ({
        ...styles,
        backgroundColor: isFocused ? '#fff' : '#1c1c1c',
        color: isFocused ? '#1c1c1c' : '#FFF'
    })
};

export default function ChangeLanguage() {
    const { t, currentLanguage, onChangeLang } = useLocales();
    const [language, setLanguage] = useState(options.find((e) => e.value === currentLanguage));

    const onChangeLanguage = () => {
        onChangeLang(language.value);
    };

    return (
        <>
            <input type="checkbox" id="language-modal-check" className="modal-toggle" />
            <div className="modal">
                <BlurModalBox className="modal-box backdrop-blur-lg">
                    <div className="mb-2">
                        <label
                            htmlFor="language-modal-check"
                            className="btn btn-sm btn-circle absolute right-2 top-5 bg-transparent border-0"
                        >
                            ✕
                        </label>
                        <h3 className="font-bold text-lg">{t('title.modal.change_language')}</h3>
                    </div>
                    <hr className="-mx-5 border-stone-500" />
                    <div className="flex flex-col gap-2 p-2 pt-4">
                        <Select
                            value={language}
                            onChange={(lang) => setLanguage(lang)}
                            options={options}
                            className="red"
                            styles={colourStyles}
                        />
                    </div>
                    <div className="flex justify-end gap-2 p-2">
                        <button className="btn btn-primary px-8 capitalize" onClick={onChangeLanguage}>
                            {t('action.change')}
                        </button>
                    </div>
                </BlurModalBox>
            </div>
        </>
    );
}
